<template>
	<view class="page commonPageTitle">
		<defaultTitle :dto="defaultTitleDto"></defaultTitle>
		<view class="tabTitle promotionTitle">
			<view :class="orderType=='0'?'active':''" @click="chooseChange(0)">全部</view>
			<view :class="orderType=='1'?'active':''" @click="chooseChange(1)">待结算</view>
			<view :class="orderType=='2'?'active':''" @click="chooseChange(2)">已结算</view>
			<view :class="orderType=='3'?'active':''" @click="chooseChange(3)">已关闭</view>
		</view>
		<scroll-view :scroll-top="scrollTop" scroll-y="true" @scrolltolower='scrolltolower'
		 lower-threshold='150' class="promotionView">
			<block>
				<orderlist :list="orderColumns.records"></orderlist>
			</block>
			<view class="hasNothingMsg" v-if="orderColumns.records.length==0">
				<image src="http://beifa-mall.oss-cn-shanghai.aliyuncs.com/appletsImages/noOrder.png" mode="widthFix"></image>
				<text class="hasNothingMsgText">暂无订单</text>
			</view>  
		</scroll-view>
	</view>
</template>

<script>
	import orderlist from '@/copartner/intermediaryOrder/orderlist.vue'
	import memberApi from '@/api/member/index.js'
	
	export default {
		data(){
			return{
				defaultTitleDto:{
					title:'居间订单'
				},
				orderType:0,
				orderState:'',
				pageInfo:{
					pageNo:1,
					pageSize:10
				},
				timeRange:0,//查询时间范围 0、日 1、月
				orderColumns: {
					records: [ 
						{
							"id":"1400704039846268929",
							"storeId":"1326337420462661634",
							"storeName":"店铺名称",
							"payPrice":6.5,
							"lockState":0,
							"orderState":20,
							"refundState":0,
							"freeOrder":false,
							"orderType":0,
							"packageType":0,
							"tuanId":"0",
							"payDeadLine":1622796042000,
							"commentFlag":0,
							"actualFreight":1,
							"payId":"1400704039686885377",
							"score":0,
							"extendShip":0,
						},
						{
							"id":"1400704039846268929",
							"storeId":"1326337420462661634",
							"storeName":"店铺名称",
							"payPrice":6.5,
							"lockState":0,
							"orderState":20,
							"refundState":0,
							"freeOrder":false,
							"orderType":0,
							"packageType":0,
							"tuanId":"0",
							"payDeadLine":1622796042000,
							"commentFlag":0,
							"actualFreight":1,
							"payId":"1400704039686885377",
							"score":0,
							"extendShip":0,
						},
					]
				},
				userVipInfo:[],//个人会员
				corporateMemberInfo:[],//企业会员
				commodityInfo:[],//商品推广
				scrollTop:0,
			}
		},
		onShow() {
			this.timeRange = this.$Route.query.timeRange||0
		},
		methods:{
			scrolltolower() { //滚动到底部
				if (this.pageInfo.pageNo >= this.orderColumns.totalPage) {
					return
				}
				this.pageInfo.pageNo++;
				this.queryOrders(true)
			},
			chooseChange(type){
				this.orderType = type||type==0?type:''
				this.pageInfo.pageNo = 1;
				this.$nextTick(() => {
					this.scrollTop = 0;
				})
				this.queryOrders()
			},
			orderChange(type){ //商品推广切换
				this.orderState = type?type:''
				this.pageInfo.pageNo = 1;
				this.$nextTick(() => {
					this.scrollTop = 0;
				})
				this.queryOrders()
			},
			queryOrders(flag){ // 
				uni.showLoading({
					title: '加载中...'
				});
				let parmas = {
					...this.pageInfo,
					orderType:this.orderType,
					timeRange:this.timeRange
				}
				if(this.orderType==2 && this.orderState){
					parmas.orderState = this.orderState
				}
				memberApi.estimateDetail(parmas).then(res => {
					uni.hideLoading()
					if (res.data.code == 200) {
						if (res.data.data) {
							if (flag) {
								this.orderColumns.records = this.orderColumns.records.concat(res.data.data.records)
							} else {
								this.orderColumns = res.data.data;
							}
						} else {
							this.orderColumns.records.splice(0);
							this.orderColumns.totalPage = 0;
						}
					} else {
						uni.showToast({
							title: res.data.message,
							icon: "none"
						})
					}
				})
			},
		},
		mounted() {
			// this.queryOrders();	
		},
		components:{
			orderlist,
		}
	}
</script>

